import React from "react";
import { MessageType } from "../../models/MessageType.model";
import styled from "styled-components";

const AlertBox = styled.div`
  margin: 8px auto;
  padding: 16px 30px;
  position: relative;
  border-radius: 5px;
  box-shadow: 0 0 15px 5px #ccc;
`;

const ErrorMessage = styled(AlertBox)`
  background-color: #f7a7a3;
  border-left: 5px solid #8f130c;
`;

const SimpleMessage = styled(AlertBox)`
  background-color: #ebebeb;
  border-left: 5px solid #6c6c6c;
`;

const SuccessMessage = styled(AlertBox)`
  background-color: #a8f0c6;
  border-left: 5px solid #178344;
`;

const WarningMessage = styled(AlertBox)`
  ackground-color: #ffd48a;
  border-left: 5px solid #8a5700;
`;

interface MessageProps {
  type: MessageType;
  text: string;
}

function Message({ type, text }: MessageProps) {
  return (
    <div>
      {type === MessageType.Error && <ErrorMessage>{text}</ErrorMessage>}
      {type === MessageType.Simple && <SimpleMessage>{text}</SimpleMessage>}
    </div>
  );
}

export default Message;
