  pad = (n: string, width: number) => {
    let z = "0";
    n = n + "";
    return n.length >= width ? n : new Array(width - n.length + 1).join(z) + n;
  };

  toDateTimeString = (timestamp: number): string => {
    let d = new Date(timestamp * 1000);

    return (
      this.pad(d.getDate().toString(), 2) +
      "." +
      this.pad((d.getMonth() + 1).toString(), 2) +
      "." +
      d.getFullYear() +
      " - " +
      d.getHours() +
      ":" +
      d.getMinutes()
    );
  };

