import * as React from "react";
import { Box, Text, Space, TextInput } from "@mantine/core";
import { ConnectionFormData } from "../../models/ConnectionFormData.model";
import { FormProps } from "../../models/FormProps.model";

function Connect({ formData, setFormData }: FormProps<ConnectionFormData>) {
  return (
    <React.Fragment>
      <Text sx={{ fontSize: "2.3rem" }}>Join Game</Text>

      <Box
        sx={{
          margin: "1rem 0",
        }}
      >
        <TextInput
          onChange={(e) => {
            setFormData({
              ...formData,
              nickname: e.target.value,
            });
          }}
          value={formData.nickname}
          placeholder="Nickname"
          size="lg"
          required
        />
        <Space h="xs" />
        <TextInput
          onChange={(e) => {
            setFormData({
              ...formData,
              key: e.target.value,
            });
          }}
          value={formData.key}
          placeholder="Game key"
          defaultValue={"16935763061hUJiF"}
          size="lg"
          required
        />
      </Box>
    </React.Fragment>
  );
}
export default Connect;
