import { ApiResponse } from "../models/Response.model";
import { AuthMode } from "../models/AuthMode";
import RequestUtil from "../utils/RequestUtil";
import { GAME_API_URL } from "../api-config";

export default class GameApi {
  static connectToGame = async (
    key: string,
    nickname: string
  ): Promise<ApiResponse<any>> => {
    return await RequestUtil.fetch<ApiResponse<any>>(
      `${GAME_API_URL}/connect`,
      "POST",
      AuthMode.none,
      {
        key: key,
        nickname: nickname,
      }
    );
  };

  static createAvatar = async (avatar: Blob): Promise<ApiResponse<any>> => {
    return await RequestUtil.fetch<ApiResponse<any>>(
      `${GAME_API_URL}/avatar`,
      "POST",
      AuthMode.wsBearer,
      {},
      { avatar: { blob: avatar, filename: "avatar.svg" } }
    );
  };

  static getInstancePlayers = async (): Promise<ApiResponse<any>> => {
    return await RequestUtil.fetch<ApiResponse<any>>(
      `${GAME_API_URL}/instancePlayers`,
      "GET",
      AuthMode.wsBearer
    );
  };

  static exitSession = async (): Promise<ApiResponse<any>> => {
    return await RequestUtil.fetch<ApiResponse<any>>(
      `${GAME_API_URL}/exitSession`,
      "GET",
      AuthMode.wsBearer
    );
  };
}
