interface TWindow extends Window {
  redirectAfterConnectionUrl: string;
  baseUrl: string;
  jwtToken: string;
  userId: number;
  instanceId: number;
}

const tWindow = window as unknown as TWindow;

export const baseUrl: string | undefined =
  !process.env.NODE_ENV || process.env.NODE_ENV === "development"
    ? (process.env.REACT_APP_BASE_URL as string)
    : tWindow.baseUrl;

export const redirectAfterConnectionUrl: string =
  !process.env.NODE_ENV || process.env.NODE_ENV === "development"
    ? (process.env.REACT_APP_GAME_PAGE as string)
    : tWindow.redirectAfterConnectionUrl;

export const jwtToken: string =
  !process.env.NODE_ENV || process.env.NODE_ENV === "development"
    ? ""
    : tWindow.jwtToken;

export const userId: number =
  !process.env.NODE_ENV || process.env.NODE_ENV === "development"
    ? -1
    : Number(tWindow.userId);

export const instanceId: number =
  !process.env.NODE_ENV || process.env.NODE_ENV === "development"
    ? -1
    : Number(tWindow.instanceId);
