import "./App.css";
import Form from "./components/form";
import React, { useEffect } from "react";
import history from "./history";

function App() {
  const usedHistory = history;
  const [, updateComponent] = React.useState({});
  const forceUpdateComponent = React.useCallback(() => updateComponent({}), []);

  useEffect(() => {
    // This function will run whenever the URL changes
    const unlisten = usedHistory.listen(() => {
      forceUpdateComponent();
    });

    // Return a cleanup function to unsubscribe from the history listener
    return () => {
      unlisten();
    };
  }, [usedHistory]);

  return (
    <div className="App">
      <Form />
    </div>
  );
}

export default App;
