import * as React from "react";
import { useEffect, useState } from "react";

import { Box, Text, Space } from "@mantine/core";
import { FormProps } from "../models/FormProps.model";
import { PlayerData } from "../models/GetInstancePlayersResponse.model";
import GameApi from "../api/GameApi";
import { ApiResponse } from "../models/Response.model";
import styled from "styled-components";
import { GetInstancePlayersResponse } from "../models/GetInstancePlayersResponse.model";
import { baseUrl, redirectAfterConnectionUrl } from "../utils/Constants";
import WebStorage from "../utils/WebStorage";

interface StartGameProps {
  ownId: number;
}

const Container = styled.div`
  max-width: 1040px;
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px; /* Adjust as needed for spacing between images */
  justify-content: start;
`;

const AvatarContainer = styled.div`
  box-sizing: border-box; /* Includes padding and border in the width calculation */
  padding: 10px; /* Add spacing between images */
`;

const AvatarImage = styled.img`
  max-width: 150px;
`;

const Link = styled.a`
  display: block;
  background-color: #d32f2f;
  color: white;
  border-radius: 30px;
  margin: 10px 0;
  min-width: 160px;
  letter-spacing: 1.3px;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  width: 300px;
  margin: auto;
`;

const StartLink = styled(Link)`
  background-color: #334150;
  padding: 18px 38px;
  &:hover {
    background-color: #272f37;
    text-decoration: none;
  }
`;

const ExitLink = styled(Link)`
  background-color: #8b0a00;
  margin-top: 16px;
  padding: 10px 38px;
  & {
    color: white;
  }
  &:hover {
    background-color: #770700;
    text-decoration: none;
  }
`;

function StartGame({ ownId }: StartGameProps) {
  const [activePlayers, setActivePlayers] = useState<PlayerData[]>([]);

  useEffect(() => {
    // Function to fetch data from the API
    const fetchData = async () => {
      try {
        const r: ApiResponse<GetInstancePlayersResponse> =
          await GameApi.getInstancePlayers();
        if (r.status === 200) {
          setActivePlayers(r.data?.players as PlayerData[]);
        } else {
          throw new Error("Network response was not ok");
        }
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    // Fetch data initially
    fetchData();

    // Set up an interval to fetch data every 5 seconds
    const intervalId = setInterval(() => {
      fetchData();
    }, 5000);

    // Clean up the interval when the component unmounts
    return () => clearInterval(intervalId);
  }, []); // Empty dependency array to run this effect only once on component mount

  return (
    <React.Fragment>
      <h3>Ready?</h3>
      <Box
        sx={{
          margin: "1rem 0",
        }}
      >
        {activePlayers.length > 0 && (
          <Container>
            {activePlayers.map((p: PlayerData) => {
              return (
                <AvatarContainer
                  data-toggle="tooltip"
                  data-placement="bottom"
                  title={p.nickname}
                >
                  <AvatarImage src={`${baseUrl}/${p.avatarUrl}`} alt="" />
                  <Text>
                    {p.nickname} {p.playerSessionUid === ownId && "(You)"}
                  </Text>
                </AvatarContainer>
              );
            })}
          </Container>
        )}
        <StartLink
          href={
            redirectAfterConnectionUrl +
            "&tx_gtnescapegame_gtnescapegameplugin[gameId]=" +
            WebStorage.getItem("ws.instanceId")
          }
        >
          Enter Game
        </StartLink>
        <ExitLink onClick={handleExit}>Exit Session</ExitLink>
      </Box>
    </React.Fragment>
  );

  async function handleExit() {
    await GameApi.exitSession();
    window.location.reload();
  }
}
export default StartGame;
