export default class WebStorage {
  static storage: Storage = localStorage;

  static getItem = (item: string, type: string = "string"): any => {
    switch (type) {
      case "string":
        return JSON.parse(WebStorage.storage.getItem(item) as string);
      case "number":
        return Number(JSON.parse(WebStorage.storage.getItem(item) as string));
    }
  };

  static hasItem = (item: string): boolean => {
    let hasItem = WebStorage.getItem(item) != null ? true : false;
    return hasItem;
  };

  static setItem = (item: string, value: any) => {
    WebStorage.storage.setItem(item, JSON.stringify(value));
  };

  static clearItem = (item: string) => {
    WebStorage.storage.removeItem(item);
  };
}
