import { useEffect, useState } from "react";
import { Flex, Box, Button, Title, Text } from "@mantine/core";
import Connect from "./formparts/connect";
import AvatarCreator from "./formparts/avatar-creator";
import GameApi from "../api/GameApi";
import { ApiResponse } from "../models/Response.model";
import { ConnectionResponse } from "../models/ConnectionResponse.model";
import { ConnectionFormData } from "../models/ConnectionFormData.model";
import LoadingWrapper from "./loading-wrapper";
import FlashMessage from "react-flash-message";
import { Toaster } from "react-hot-toast";
import { MessageType } from "../models/MessageType.model";
import Message from "./utils/Message";
import ToastForm from "./toast-notification/form/toast-form";
import React from "react";
import WebStorage from "../utils/WebStorage";
import { PlayerData } from "../models/GetInstancePlayersResponse.model";
import StartGame from "./start-game";
import { jwtToken, userId, instanceId } from "../utils/Constants";

const nextButtonText: Map<number, string> = new Map<number, string>([
  [0, "Connect"],
  [1, "Submit"],
]);

function Form() {
  const [page, setPage] = useState(jwtToken != null && jwtToken !== "" ? 2 : 0);
  const [isLoading, setIsLoading] = useState(false);
  const [formData, setFormData] = useState<ConnectionFormData>({
    key: "",
    nickname: "",
    avatar: null,
  });
  const [doShowMessage, setDoShowMessage] = useState<boolean>(false);
  //  const [message, setMessage] = useState<string>("");
  const [message, setMessage] = useState<JSX.Element>();

  useEffect(() => {
    if (jwtToken != null && jwtToken !== "") {
      WebStorage.setItem("ws.token", jwtToken);
      WebStorage.setItem("ws.userId", userId);
      WebStorage.setItem("ws.instanceId", instanceId);
    }
  }, []); // The empty dependency array ensures that the effect runs only once, similar to componentDidMount

  const props = {
    formData: formData,
    setFormData: setFormData,
  };

  const conditionalComponent = () => {
    switch (page) {
      case 0:
        return (
          <LoadingWrapper
            component={
              <Connect formData={formData} setFormData={setFormData} />
            }
            isLoading={isLoading}
          />
        );
      case 1:
        return (
          <LoadingWrapper
            component={<AvatarCreator {...props} />}
            isLoading={isLoading}
          />
        );

      default:
        return <StartGame ownId={WebStorage.getItem("ws.userId")} />;
    }
  };

  return (
    <Box>
      {/*<Title
        sx={{
          textAlign: "center",
        }}
        order={2}
      >
        Connect to game
      </Title> */}
      {conditionalComponent()}
      {doShowMessage && (
        <Box
          sx={{
            width: "100%",
            margin: "0px auto",
            textAlign: "center",
            padding: "0px 0",
          }}
        >
          {message}
        </Box>
      )}
      <Box sx={{ margin: "0 1.5rem" }}>
        {page > 0 && page < nextButtonText.size && (
          <Button
            sx={{ margin: "1rem" }}
            color="teal"
            radius="xs"
            size="lg"
            onClick={() => setPage(page - 1)}
          >
            Back
          </Button>
        )}
        {page < nextButtonText.size && (
          <Button
            sx={{ margin: "0 1.5rem" }}
            color="teal"
            radius="xs"
            size="lg"
            onClick={handleSubmit}
          >
            {nextButtonText.get(page)}
          </Button>
        )}
      </Box>
    </Box>
  );

  function showMessage(type: MessageType, m: string) {
    setIsLoading(false);
    setMessage(<Message type={type} text={m} />);
    setDoShowMessage(true);
    setTimeout(() => {
      setDoShowMessage(false);
    }, 8000);
  }

  async function handleSubmit() {
    setDoShowMessage(false);

    //setIsLoading(true);
    if (page === 0) {
      const r: ApiResponse<ConnectionResponse> = await GameApi.connectToGame(
        formData.key,
        formData.nickname
      );
      if (r.status === 200) {
        WebStorage.setItem("ws.token", r.data?.token);
        WebStorage.setItem("ws.userId", r.data?.userId);
        WebStorage.setItem("ws.instanceId", r.data?.instanceId);
      } else {
        showMessage(MessageType.Error, r.error as string);
        return;
      }
    } else if (page === 1) {
      const r: ApiResponse<any> = await GameApi.createAvatar(
        formData.avatar as Blob
      );
      if (r.status === 200) {
      } else {
        showMessage(MessageType.Error, r.error as string);
        return;
      }
    }
    setPage(page + 1);
    setIsLoading(false);
  }
}

export default Form;
